import { useEffect, useState } from "react";
import { HiOutlineXMark } from "react-icons/hi2";
import Modal from "react-modal";

const customStyles = {
  content: {
    height: "fit-content",
    background: "none",
    padding: "0",
    display: "flex",
    justifyContent: "center",
    border: "0",
  },
};

const AddPrecinct = (props) => {
  const { isOpen, onClose, onSave } = props;

  const [showWarning, setShowWarning] = useState(false);
  const [precinctName, setPrecinctName] = useState("");
  const [countOne, setCountOne] = useState(0);
  const [countTwo, setCountTwo] = useState(0);
  const [countThree, setCountThree] = useState(0);

  useEffect(() => {
    if (!isOpen) return;
    setPrecinctName("");
    setShowWarning(false);
    setCountOne(0);
    setCountTwo(0);
    setCountThree(0);
  }, [isOpen]);

  const handleClose = () => {
    if (precinctName || countOne > 0 || countTwo > 0 || countThree > 0) {
      setShowWarning(true);
      return;
    }
    onClose?.();
  };

  const handleSave = () => {
    if (!precinctName) {
      alert("Please Enter Precinct Name!");
      return;
    }
    const list = JSON.parse(localStorage.getItem("list")) ?? [];
    const data = {
      precinctName,
      divinagracia: countOne,
      matinong: countTwo,
      garlan: countThree,
    };
    console.log(data, "<=== data");
    const newList = [...list, data];
    localStorage.setItem("list", JSON.stringify(newList));
    onSave?.(data);
  };

  return (
    <Modal isOpen={isOpen} style={customStyles}>
      <div className="w-full max-w-xl bg-white border border-gray-200 rounded-lg ">
        <div className="flex justify-between px-4 pt-4">
          <p className="text-2xl font-semibold">Add Precinct</p>
          <button
            className="inline-block text-gray-500 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-700 focus:outline-none rounded-full text-sm p-1.5"
            type="button"
            onClick={handleClose}
          >
            <HiOutlineXMark className="text-2xl" />
          </button>
        </div>
        <div className="px-6 py-4">
          {showWarning && (
            <div
              className="p-4 mb-4 text-sm text-red-800 rounded-lg bg-red-50 dark:text-red-400"
              role="alert"
            >
              <span className="font-medium">Warning!</span> You can't go back
              yet, Are you sure do you want to close this?{" "}
              <span
                className="cursor-pointer underline font-medium"
                onClick={() => onClose?.()}
              >
                Close!
              </span>
            </div>
          )}
          <section className="grid grid-cols-2 gap-4 mb-6">
            <div className="mb-5">
              <label
                for="precinct"
                className="block mb-2 text-sm font-medium text-gray-900"
              >
                Precinct
              </label>
              <input
                type="text"
                id="precinct"
                value={precinctName}
                onChange={(e) => {
                  setPrecinctName(e.target.value);
                  setShowWarning(false);
                }}
                className="border border-gray-300 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:border-gray-600 dark:placeholder-gray-400 dark:focus:ring-blue-500 dark:focus:border-blue-500"
              />
            </div>
          </section>
          <section className="grid grid-cols-3 gap-4 mb-2">
            <div className="flex flex-col items-center gap-4">
              <p className="text-5xl font-bold">{countOne}</p>
              <button
                className="w-[150px] h-[150px] mb-3 rounded-full shadow-lg bg-[#fc0398] text-xl text-white font-semibold"
                onClick={() => {
                  setCountOne(countOne + 1);
                  setShowWarning(false);
                }}
              >
                Divinagracia
              </button>
            </div>
            <div className="flex flex-col items-center gap-4">
              <p className="text-5xl font-bold">{countTwo}</p>
              <button
                className="w-[150px] h-[150px] mb-3 rounded-full shadow-lg bg-[#0331fc] text-xl text-white font-semibold"
                onClick={() => {
                  setCountTwo(countTwo + 1);
                  setShowWarning(false);
                }}
              >
                Matinong
              </button>
            </div>
            <div className="flex flex-col items-center gap-4">
              <p className="text-5xl font-bold">{countThree}</p>
              <button
                className="w-[150px] h-[150px] mb-3 rounded-full shadow-lg bg-[#eb4034] text-xl text-white font-semibold"
                onClick={() => {
                  setCountThree(countThree + 1);
                  setShowWarning(false);
                }}
              >
                Garlan
              </button>
            </div>
          </section>
        </div>
        <div className="flex justify-end px-4 pb-4">
          <button
            type="button"
            className="text-gray-900 bg-gradient-to-r from-red-200 via-red-300 to-yellow-200 hover:bg-gradient-to-bl focus:ring-4 focus:outline-none focus:ring-red-100 dark:focus:ring-red-400 font-medium rounded-lg text-sm px-5 py-2.5 text-center me-2 mb-2"
            onClick={handleSave}
          >
            Save Precinct
          </button>
        </div>
      </div>
    </Modal>
  );
};

export default AddPrecinct;
