import React, { useEffect, useState } from "react";
import { HiOutlinePlusCircle } from "react-icons/hi2";
import Table from "./Table";
import AddPrecinct from "./AddPrecinct";

const Layout = () => {
  const [modalIsOpen, setIsOpen] = useState(false);
  const [list, setList] = useState();

  useEffect(() => {
    const data = JSON.parse(localStorage.getItem("list")) ?? [];
    setList(data);
  }, []);

  return (
    <section className="w-full">
      <header className="flex justify-end py-2 mb-2">
        <button
          type="button"
          className="text-gray-900 bg-white hover:bg-gray-100 border border-gray-200 focus:outline-none font-medium rounded-lg text-sm px-5 py-2.5 text-center inline-flex items-center dark:focus:ring-gray-600 dark:bg-gray-800 dark:border-gray-700 dark:text-white dark:hover:bg-gray-700"
          onClick={() => setIsOpen(true)}
        >
          <HiOutlinePlusCircle className="text-2xl mr-2" />
          Add Precinct
        </button>
      </header>
      <Table list={list} />
      <AddPrecinct
        isOpen={modalIsOpen}
        onClose={() => setIsOpen(false)}
        onSave={(data) => {
          setList([...list, data]);
          setIsOpen(false);
        }}
      />
    </section>
  );
};

export default Layout;
