import { useState } from "react";
import { HiOutlineScale, HiOutlineTrash } from "react-icons/hi2";
import Modal from "react-modal";

const customStyles = {
  content: {
    height: "fit-content",
    background: "none",
    padding: "0",
    display: "flex",
    justifyContent: "center",
    border: "0",
  },
};

const Confirmation = (props) => {
  const { isOpen, onClose } = props;

  const onReset = () => {
    localStorage.removeItem("list");
  };

  return (
    <Modal isOpen={isOpen} style={customStyles} onRequestClose={onClose}>
      <div className="w-full max-w-lg bg-white border border-gray-200 rounded-lg p-6">
        <p className="text-2xl font-semibold mb-8">
          Are you sure do you want to reset?
        </p>
        <div className="flex gap-4 justify-end">
          <button
            class="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded"
            onClick={() => {
              onReset();
              onClose?.();
              window.location.reload();
            }}
          >
            Reset
          </button>
          <button
            class="hover:bg-[#e0e0e0] border font-bold py-2 px-4 rounded"
            onClick={onClose}
          >
            Close
          </button>
        </div>
      </div>
    </Modal>
  );
};

const Navbar = () => {
  const [showConfirm, setShowConfirm] = useState(false);

  return (
    <>
      <nav className="bg-white border-gray-200 dark:bg-gray-900">
        <div className="max-w-screen-xl flex flex-wrap items-center justify-between mx-auto p-4">
          <a className="flex items-center space-x-3 rtl:space-x-reverse">
            <HiOutlineScale className="text-2xl text-white" />
            <span className="self-center text-2xl font-semibold whitespace-nowrap dark:text-white">
              Vote Count
            </span>
          </a>

          <div className="absolute inset-y-0 right-0 flex items-center pr-2 sm:static sm:inset-auto sm:ml-6 sm:pr-0">
            <button
              type="button"
              title="Reset Data"
              className="relative rounded-full bg-gray-800 p-1 text-gray-400 hover:text-white focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-gray-800 focus:outline-hidden"
              onClick={() => setShowConfirm(true)}
            >
              <HiOutlineTrash className="text-2xl" />
            </button>
          </div>
        </div>
      </nav>
      <Confirmation
        isOpen={showConfirm}
        onClose={() => setShowConfirm(false)}
      />
    </>
  );
};

export default Navbar;
