import { useMemo } from "react";

const Table = (props) => {
  const { list } = props;

  const total = useMemo(() => {
    const countOne = list?.reduce((acc, _item) => {
      return acc + _item.divinagracia;
    }, 0);
    console.log(countOne, "<=== countOne");
    const countTwo = list?.reduce((acc, _item) => {
      return acc + _item.matinong;
    }, 0);
    console.log(countTwo, "<=== countTwo");
    const countThree = list?.reduce((acc, _item) => {
      return acc + _item.garlan;
    }, 0);
    console.log(countThree, "<=== countThree");
    return { totalOne: countOne, totalTwo: countTwo, totalThree: countThree };
  }, [list]);

  return (
    <div className="block w-full p-6 bg-white border border-gray-200 rounded-lg shadow-sm dark:bg-gray-800 dark:border-gray-700">
      <table className="w-full text-sm text-left rtl:text-right text-gray-500 dark:text-gray-400">
        <thead className="text-xs text-gray-700 uppercase bg-gray-50 dark:bg-gray-700 dark:text-gray-400">
          <tr>
            <th scope="col" className="px-6 py-3">
              Precinct Name
            </th>
            <th scope="col" className="px-6 py-3">
              Divinagracia
            </th>
            <th scope="col" className="px-6 py-3">
              Matinong
            </th>
            <th scope="col" className="px-6 py-3">
              Garlan
            </th>
          </tr>
        </thead>
        <tbody>
          {Boolean(list && list?.length) &&
            list.map((_item, idx) => (
              <tr
                className="bg-white border-b dark:bg-gray-800 dark:border-gray-700 border-gray-200"
                key={idx}
              >
                <th
                  scope="row"
                  className="px-6 py-4 font-medium text-gray-900 whitespace-nowrap dark:text-white"
                >
                  {_item.precinctName}
                </th>
                <td className="px-6 py-4">{_item.divinagracia}</td>
                <td className="px-6 py-4">{_item.matinong}</td>
                <td className="px-6 py-4">{_item.garlan}</td>
              </tr>
            ))}

          <tr className="bg-white border-b dark:bg-gray-800 dark:border-gray-700 border-gray-200">
            <th
              scope="row"
              className="px-6 py-4 font-medium text-gray-900 whitespace-nowrap dark:text-white"
            >
              Total
            </th>
            <th
              scope="row"
              className="px-6 py-4 font-medium text-gray-900 whitespace-nowrap dark:text-white"
            >
              {total?.totalOne}
            </th>
            <th
              scope="row"
              className="px-6 py-4 font-medium text-gray-900 whitespace-nowrap dark:text-white"
            >
              {total?.totalTwo}
            </th>
            <th
              scope="row"
              className="px-6 py-4 font-medium text-gray-900 whitespace-nowrap dark:text-white"
            >
              {total?.totalThree}
            </th>
          </tr>
        </tbody>
      </table>
    </div>
  );
};

export default Table;
